import React, { useEffect, useState } from 'react';
import { fetchProducts } from './product-api';
import { Loading } from 'components/Loader/Loading';
import ProductItem from './ProductItem';
import { ProductWrap } from './Product.styled';
import Filters from './Filters';
import { nanoid } from '@reduxjs/toolkit';

export default function Products() {
  const [products, setProducts] = useState();
  const [isLoading, setIsLoading] = useState(false);
  // const [filters, setFilters] = useState(null);
  const [filters, setFilters] = useState({
    department: '',
    name: '',
    material: '',
    country: '',
  });
  console.log(filters);

  useEffect(() => {
    getProducts();
    return () => {};
  }, []);

  const getProducts = async () => {
    try {
      setIsLoading(true);
      const response = await fetchProducts();
      setProducts(response);
    } catch (error) {
      console(error.message);
    } finally {
      setIsLoading(false);
    }
  };

  const getFilter = event => {
    // const newFilter = { [event.target.name]: event.target.value };
    // console.log(newFilter);
    setFilters(prevFilters => ({
      ...prevFilters,
      [event.target.name]: event.target.value,
    }));
  };

  const clearFilter = () => {
    setFilters({
      department: '',
      name: '',
      material: '',
      country: '',
    });
  };

  function foo(products, filters) {
    console.log('Calculating visible tasks');
    console.log(products);
    // let result = [];
    // for (let i = 0; i < products.length; i++) {
    if (filters.department !== '') {
      products = products.filter(product =>
        product.department.includes(filters.department)
      );
    }
    if (filters.name !== '') {
      products = products.filter(product =>
        product.name.includes(filters.name)
      );
    }
    if (filters.material !== '') {
      products = products.filter(product =>
        product.material.includes(filters.material)
      );
    }
    if (filters.country !== '') {
      products = products.filter(product =>
        product.country.includes(filters.country)
      );
    }
    // default:
    return products;
    // }
  }
  const productsF = foo(products, filters);

  const Arr = Array.isArray(productsF) && productsF.length > 0;
  return (
    <div>
      {Arr && (
        <Filters
          getFilter={getFilter}
          products={products}
          filters={filters}
          clearFilter={clearFilter}
        />
      )}

      {isLoading && <Loading />}
      <ProductWrap>
        {Arr &&
          productsF.map(item => {
            return <ProductItem key={nanoid()} item={item} />;
          })}
      </ProductWrap>
    </div>
  );
}

